import React, {useEffect, useRef} from 'react';
import gsap from 'gsap';

import './css/imagesLink.css';

import spring from './img/spring.jpg';
import summer from './img/summer.jpg';
import autumn from './img/autumn.jpg';
import winter from './img/winter.jpg';


const ImagesLink = () => {
    const seasons = [
        {name: 'Spring', image: spring},
        {name: 'Summer', image: summer},
        {name: 'Autumn', image: autumn},
        {name: 'Winter', image: winter}
    ];
    
    let seasonsBackground;
    
    const handleSeasons = season =>{
        gsap.to(seasonsBackground, {
            duration: 0,
            background: `center center/ 130% no-repeat  url(${season})` 
        });
        
        gsap.to(seasonsBackground, {
            duration: 0.4,
            opacity: 1,
            ease: 'power3.inOut'
        });
        
        gsap.from(seasonsBackground, {
            duration: 0.4,
            skewY: 2,
            transformOrigin: 'right top'
        });
        
    }
    
    
    return(
        <div className="imgContainer">
            <div ref={el => (seasonsBackground = el)} className="bg"></div>
            <div className="imgLink">
                {seasons.map(el => (
                    <span key={el.name} onClick={() => handleSeasons(el.image)} >
                        {el.name}
                    </span>
                ))}
            </div>
        </div>
    );
}

export default ImagesLink;